<script>
  import {
    mode,
    clickformopen,
  } from "./store.js";
  import { get } from "svelte/store";

  let pw = "";
  let newemail = "";


  async function changepw() {

    const emaildata = {
      nowpw: pw,
      newemail: newemail,
    };
    let checkjwt = localStorage.getItem("d2rpvpjwtToken")
    if (checkjwt) {
      try {
        const endpoint = $mode
          ? "/process_changeemail_m"
          : "/process_changeemail";
        const response = await fetch(endpoint, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(emaildata), // Send the JSON data as the body of the request
        });
        if (response.status === 200) {
        alert("이메일 변경 완료");
        clickformopen(null);
      }
       else if (response.status===403) {
          await refresh_access_token()
           checkjwt = localStorage.getItem("d2rpvpjwtToken")
        
           const response = await fetch(endpoint, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
          body: JSON.stringify(emaildata), // Send the JSON data as the body of the request
        });
        alert("이메일 변경 완료");
        clickformopen(null);

          }

      } catch (error) {
        alert("오류 발생 :", error);
      }
    } else {
      alert("다시 로그인해주세요");
    }
  }
</script>

<div class="left">
  암호
  <input class="namewidth" type="password" bind:value={pw} />
  새 이메일
  <input class="namewidth" type="text" bind:value={newemail} />
  <button on:click={() => changepw()}>이메일 변경</button>
</div>
